.buttom{
    width: 100%;
    height: 50px;
    border-radius: 20px;
    background-color: #59B256;
    position: sticky;
    bottom: 0;
    display: flex;
    justify-content: center;
    padding: 0 -11px 0 -11px;
    align-items: center;
    justify-content: space-between;
    align-items: center;
    z-index: 999999999999;
}
.active{
    transition: all 9s ease;
}
.active:active{
    color:lightgoldenrodyellow;
    transform: scale(1.1);
}


a{
    text-decoration: none;
    color: white;
    /* justify-content: center; */
}
a:hover{
    color: rgb(248, 234, 112);

}
