
.f{
    height: 45vh;
    position: relative;
}
::selection{
    background-color: #000000;
    color: red;
}
::-moz-selection{
    background-color: #000000;
    color: red;
}
.f-bg{
    width: 20px;
    margin-top:50px;
    height: 70%;
    background-color: #59b256;
    position: absolute;
}
.f-wrapper{
    padding: 50px;
    display: flex;
}
.f-left{
    flex: 1;
    margin-right: 10px;
}
.f-right{
    flex: 1;
}
.f-title{
    font-weight: 500;
    font-size: 50px;
    margin-top: 7%;
    width: 95%;
}
.f-desc{
    font-size: 20px;
    margin: 15px;
}
.f-icon{
    width: 50px;
    height: 50px;
    margin-right: 20px;
    cursor: pointer;
}
.f-info-item{
    transition: all 0.5s ease;

}
.f-info-item:hover{
    transform: scale(1.1);
}
.f-info{
    display: flex;
    /* flex-wrap: wrap;
    flex-direction: row; */
    margin: 15px;
}
.esathi{
    border-radius: 26%;
    margin-top: 4px;
    width: 38px;
    height: 38px;
}



@media screen and (max-width: 480px){
    .f{
        height: 70vh;

    }
    .f-wrapper{
        flex-direction: column;
    }
    .f-info{
        flex-direction: row;
        flex-wrap: wrap;
    }
    .f-bg{
        height: 75%;
    }
    .f-title{
        font-size: 44px;
        width: 100%;
    }
}
@media screen and (max-width: 388px) {
    .f{
        height: 70vh;
    }
    .f-bg{
        height: 80%;
    }
    
}
@media screen and (max-width: 355px){
    .f-title{
        font-size: 44px;
    }
    /* .f{
        margin-top: 30%;

    } */
}
@media screen and (max-width: 349px){
    
    .f-desc{
        font-size: 18px;
    }
    .f-title{
        font-size: 38px;
    }
}

@media screen and (max-width: 339px){
    .f-title{
        font-size: 35px;
    }

}
@media screen and (max-width: 300px){
    .f{
        height: 80vh;
        margin-top: 50%;
    }
    .f-bg{
        height: 75%;
    }
    
}
@media screen and (max-width: 293px){
    .f{
        height: 85vh;
    }
    .f-title{
        font-size: 33px;
    }
    
}
