.pl{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
}
.pl-texts{
    width: 65%;
}
.pl-title{
    font-size: 50px;
    font-weight: 600;
}
.pl-desc{
    margin: 20px 0px;
}
.pl-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}
@media screen and (max-width: 480px) {
    .pl{
        padding: 50px 35px;
        /* overflow: scroll; */
    }
    .pl-texts{
        width: 50%;
    }
    .pl-title{
        width: 100%;
    }
    .pl-desc{
        display: none;
    }
    .pl-list{
        justify-content: space-around;
    }
}
@media screen and (max-width: 377px){
   .pl-texts{
       width: 65%;
   }
}
@media screen and (max-width: 355px){
   .pl-texts{
       width: 87%;
   }
}