
.c{
    height: 100vh;
    position: relative;
    padding-top: 20px;
}


::selection{
    background-color: #000000;
    color: red;
}
::-moz-selection{
    background-color: #000000;
    color: red;
}
.c-bg{
    width: 20px;
    height: 90vh;
    background-color: #59b256;
    position: absolute;
}
.c-wrapper{
    padding: 50px;
    display: flex;
}
.c-left{
    flex: 1;
    margin-right: 10px;
}
.c-right{
    flex: 1;
}
.c-title{
    font-size: 60px;
    width: 80%;
}
.c-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
}
.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}
.c-desc{
    font-weight: 200;
}
.c-link a{
    text-decoration: none;
    color: black;
}
form{
    margin-top: 20px; 
    position: relative;
}
input{
    width: 50%;
    border: none;
    height: 50px;
    margin: 10px 0px;
    padding-left: 5px;
    font-size: 14px;
    border-bottom: 1px solid black;
}
input:focus, textarea{
    outline: none;
}
textarea{
    width: 100%;
    resize: none;
    margin:10px 0px;
    padding: 12px 8px 8px 10px;
    font-size: 14px;
    border: none;
    border: 1px solid black;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type=number] {
    -moz-appearance: textfield;
}
.button {
    border: none;
    padding: 10px;
    color: white;
    font-weight: 500;
    cursor: pointer;
    background-color: #59B256;
    overflow: hidden;
    position: relative;
    transition: all 1s ease;
       
}

.button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: all 1s ease;
}

.button span:after {
    content: '\00bb';
    font-size: 48px;
    position: absolute;
    opacity: 0;
    top:-29px;
    right: 100px;
    padding-right: 1px;
    transition: 0.07s;
}

/* button:active{
border-radius: 2px 50% 50% 2px;
transition: all 0.1s ease;
} */
.button:active span  {
    padding-right: 1px;
}

.button:active span:after {
    opacity: 1;
    right: 0;
}
button:after{
    content: "";
    background: #90EE90;
    display: block;
    position: absolute;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px!important;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
}
button:active:after{
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s;
}
.c-message {
    font-size: 15px;
    padding: 15px;
    margin-top: 6px;
    width: 89%;
    background-color: rgb(12, 12, 12);
    color: red;
    border-radius: 19px;
    text-align: center;
    -webkit-animation: cssAnimation 9s forwards; 
    animation: cssAnimation 9s forwards;
    position: absolute;
    top: 50vh;
    left: 0;
}
@keyframes cssAnimation {
    0%   {opacity: 1;}
    /* THIS TRANSFORM WILL GIVE POP UP ANIMATION TO MESSAGE  */
    55%  {transform: translateY(-90%);}
    90%  {opacity: 1;}
    100% {opacity: 0;}
    
}
@-webkit-keyframes cssAnimation {
    0%   {opacity: 1;}
    /* THIS TRANSFORM WILL GIVE POP UP ANIMATION TO MESSAGE  */
    55%  {transform: translateY(-90%);}
    90%  {opacity: 1;}
    100% {opacity: 0;}
}
.c-link{
    transition: all 01s ease;
}
.c-link:hover{
    transform: scale(1.03);
}

@media screen and (max-width: 480px){
    .c{
        height: 155vh;
    }
    .c-wrapper{
        flex-direction: column;
        padding: 0px 50px;
    }
    .c-title{
        font-size: 30px;
    }
    .button:after{
        padding-top: 100%;
        padding-left: 124%;
        margin-left: -19px!important;
        margin-top: -64%;;
    }
    .c-info-item{
        margin: 20px 0px;
        width: 100%;
    }
    input{
        width: 90%;
    }
    .c-message{
        font-size: 12px;
        width: 100%;
        border-radius: 35px;
    }
    .button span:after {
        font-size: 20px;
    }
    .c-bg{
        height: 97%;
    }
}

@media screen and (max-width: 388px) {
    .c{
        height: 80vh;
        /* overflow: hidden; */
        /* overflow: scroll; */
        padding-bottom: 100%;
    }
    .c-bg{
        height: 90%;
    }
  
    .c-message{
        border-radius: 25px;
        top: 40vh;
    }
    .c-email{
        word-break: break-all;
    }
}
@media screen and (max-width: 355px){
    
    .c-title{
        font-size: 20px;
    }
    
}
@media screen and (max-width: 321px) {
    /* .c{
