.t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: space-around;
    z-index: 999;
    align-items: center;

}
.t-icon{
    width: 15px;
    height: 15px;
    -webkit-user-drag: none;
}
.sun{
    background-color: #fff;
    padding: 4px;
    border-radius: 50%;
}
.moon{
    background: rgb(36, 33, 33);
    padding: 4px;
    border-radius: 50%;
}
.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #999;
    position: absolute;
    cursor: pointer;
    /* left: 0; */
}