.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
    box-shadow: 0 0 #0000, 0 0 #0000, 0 10px 26px 3px rgb(0 0 0 / 22%), 0 4px 12px 2px rgb(0 0 0 / 18%);
}
.p-browser{
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: sticky;
    z-index:2;
}
.p-title{
    padding: 2px 0 0px 0;
    font-size: 8px;
    width: 100%;
    margin: 0px 6px 0 0px;
}
.p-circle{
    
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: white;
    margin: 3px;
}
.p-img{
    width: 100%;
    transition: all 10s ease;
}
.p:hover .p-img{
    transform: translateY(-78.9%);
}
/* .p-img:hover{
    transform: translateY(-100%);
} */

@media screen and (max-width: 480px) {
    .p{
        width: 85%;
        height: 75vh;
        border-radius: 10px 10px 9px 9px;
        border: 2px solid rgb(243, 242, 242);
    }
    .p-title{
        padding: 2px 0px 0px 0px;
        font-size: 10px;
        width: 100%;
        overflow: hidden;
        margin: 0px 6px 0px 0px;
    }
    .p:hover .p-img{
        transform: translateY(-60.7%);
    }
}
@media screen and (max-width: 377px){
    .p{
        height: -1%;
    }
}