.mLost {
    height: 93vh;
    position: relative;
    
}
:root{
    --tw-ring-offset-shadow: 0 0 #0000;
    --tw-ring-shadow: 0 0 #0000;
}
.mLost::after {
    content: " ";
    position: absolute;
    background: linear-gradient(90deg, rgba(128, 204, 255, .9) 0%, rgba(128, 204, 255, .6) 60%, rgba(234, 127, 223, .4) 90%);
    height: 440px;
    left: -180px;
    top: -180px;
    right: 0px;
    filter: blur(120px);
}
.lost{
    position: absolute;
    width: 80%;
    height: 28%;
    top: 200px;
    margin: 0% 10% 0px 10%;
    border-radius: 30px;
    justify-content: center;
    align-items: center;
    box-shadow: var(--tw-ring-offset-shadow,0 0 #0000),var(--tw-ring-shadow,0 0 #0000),var(--tw-shadow);
    --tw-shadow: 0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);
    

}
.heading{
    padding:40px 10% 2px 10%;
}
.heading p{
    color: teal;
    text-align: center;
    font-family:'Courier New', Courier, monospace;
    font-weight: 900;

}
.link{
    width: 70%;
    margin: 9px 15% 9px 15%;
    text-align: center;
    /* background-color: rgb(241, 77, 77); */
    border-radius: 13px;
    transition: all .9s ease;

}
.link:hover{
    transform: scale(0.8);
}
.link a{
    padding: 5px 9px 5px 9px;
    border-radius: 10px;
}



