::selection{
    color: white;
    background: teal;
}
.a{
    height: 100vh;
    display: flex;
    align-items: center;
}
.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}
.a-right{
    flex: 1;
}
.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;

}
.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}
.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.a-certificate-img{
    width: 120px;
    height: 120px;
    border-radius: 20px;
    box-shadow: 0 0 #0000,0 0 #0000,0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);

}
.a-title{
    font-weight: 400;
}
.a-sub{
    margin: 20px 8px 20px 8px;
}
.a-desc{
    font-weight: 300;
    margin: 0 32px 0 0;
}
.btn{
    margin: 50px 0 0 100px;
    padding: 13px;
    width: 40%;
    border-radius: 15px;
    text-align: center;
    justify-content: center;
    background-color: rgb(51, 185, 84);
    cursor: pointer;
    color: white;
}
.a-certificate{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.a-certificate-texts{
    width: 70%;
}
.a-certificate-title{
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}
.a-certificate-desc{
    margin: 0 32px 0 0;
}

@media screen and (max-width: 480px){
    .a{
        height: 164vh;
        flex-direction: column;
        text-align: center;
        padding-top: 50px;
    }
    .a-left{
        width: 100%;
        margin-top: 40px;
    }
    .a-card{
        height: 85%;
        width: 68%;
    }
    .a-certificate{
        display: none;
    }
    .a-right{
        padding: 20px;
        margin-top: 40px;
        height: 55%;
    }
    .a-card.bg{
        left: 30px;
    }
    .btn{
        width: 60%;
        margin: 30px 50px 0 50px;
    }
}

@media screen and (max-width: 377px){
    /* .a{
        height: 140vh;
    } */
    .a-img{
        width: 100%;
        height: 100%;
    }
    .a-card{
        width: 68%;
        height: 85%;
    }
   .btn{
       width: 50%;
     
   }
}